  addmessageO(mo){
    let mos = [];
    if(localStorage.getItem('Messageslist')){
      mos = JSON.parse(localStorage.getItem('Messageslist'));
      //剩餘引數
      mos =[...mos,mo];
    } else{
      mos =[mo];
    }
    localStorage.setItem('Messageslist',JSON.stringify(mos));
  }
