import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-not-found',
  templateUrl: './not-found.component.html',
  styleUrls: ['./not-found.component.less']
})

export class NotFoundComponent implements OnInit {

  numberdata: string;
  answer:number[];
  uniques=[{"code": 0 ,"label":"重複數字"},{"code":1 ,"label":"不重複數字"} ];
  umodel:number=0;
  isuniqe=false;
  pangramresult="";
  textdata:string;
 
 
 
  constructor() { }
 
  ngOnInit(): void {
  }
 
  orderasc(){
    if(!this.numberdata && this.numberdata!="0" ){
      return;
    }
 
    this.answer = this.arrayfix(this.numberdata).trim().split(",").map(x=>+x).sort((a,b)=>{return a-b});
    if(this.isuniqe){
      this.answer = [...new Set(this.answer)];
    }
    if(this.answer.join().includes("NaN")){
      this.numberdata = "不符格式";
    } else {
      this.numberdata = this.answer.join();
      //console.log(this.umodel);
    }
 
  }
 
  orderdesc(){
    if(!this.numberdata && this.numberdata!="0" ){
      return;
    }
 
    this.answer = this.arrayfix(this.numberdata).trim().split(",").map(x=>+x).sort((a,b)=>{return b-a});
    if(this.isuniqe){
      this.answer = [...new Set(this.answer)];
    }
    if(this.answer.join().includes("NaN")){
      this.numberdata = "不符格式";
    } else {
      this.numberdata = this.answer.join();
      //console.log(this.umodel);
    }
 
  }
 
  arrayfix(str){
    return str.replace(/\,{2,}/g, ',').replace(/^(\,*)|(\,*)$/g, '');
  }
 
  onChange(e){
    console.log(e);
    if(e == 0){
      this.isuniqe = false;
    }else{
      this.isuniqe = true;
    }
  }
 
  inputfocus(){
    this.pangramresult = "";
  }
 
  checkpangram(str){
    let flag = true;
 
    for(var i = 97; i < 123; i++){
        //a to z
        if(str.toLowerCase().includes(String.fromCharCode(i))){
           // do nothing
        } else {
          flag = false;
        }
 
        if(!flag){
          this.pangramresult = "Not pangram.不是全字母"
        }else{
          this.pangramresult = "It's pangram.全字母"
        }
    }
  }


}
